import React from 'react';
import covid19Products from "../assets/covid19_products.jpg";
import {Col, Container, Row} from "reactstrap";
import mask1 from '../assets/mask1.jpg';
import mask2 from '../assets/mask2.jpg';
import mask3 from '../assets/mask3.jpg';
import mask4 from '../assets/mask4.jpg';
import mask5 from '../assets/mask5.jpg';
import mask6 from '../assets/mask6.jpg';
import japanFlag from "../assets/japan_flag.png";


function Covid19Products() {
  return (
    <div style={{backgroundImage: `url(${covid19Products})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
      <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
        <Container>
          <Row style={{padding: '3rem 0'}}>
            <Col style={{backgroundColor: 'rgba(255, 255, 255, 0.8)', padding: '1.5rem'}}>
              <section>
                <Row>
                  <Col>
                    <img src={mask1} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                  <Col>
                    <img src={mask2} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                </Row>
                <br/>
                <Row>
                  <Col>
                    <img src={mask3} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                  <Col>
                    <img src={mask4} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                  <Col>
                    <img src={mask5} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                  <Col>
                    <img src={mask6} alt='JAPAN FLAG' style={{width: '100%'}}/>
                  </Col>
                </Row>
              </section>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default Covid19Products;
