import React, {CSSProperties, useState} from 'react';
import {HashRouter, Redirect, Route, Switch} from "react-router-dom";
import {Container, Collapse, Row, Col, Navbar, NavbarBrand, NavbarToggler, Nav, NavItem, NavLink} from 'reactstrap';
import branding from '../assets/branding.png';
import Vision from './Vision';
import Products from './Products';
import Experience from "./Experience";
import ContactUs from "./ContactUs";
import ItTalents from "./ItTalents";
import FarmingEquipment from "./FarmingEquipment";
import Covid19Products from "./Covid19Products";

const SECTION_HEIGHT = 550;
const HEADER_HEIGHT = 120;
export const SECTION_STYLES:CSSProperties = {
  height: `${SECTION_HEIGHT}px`,
  textAlign: 'center',
  display: 'table-cell',
  verticalAlign: 'middle',
}
const FOOTER_STYLES:CSSProperties = {
  height: 200,
  backgroundColor: 'rgb(19, 27, 77)',
  color: 'gray',
};


function Layout() {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);

  return (
    <div className='app'>
      <header className='app-header'>
        <Container fluid>
          <Row>
            <Col>
              <Navbar expand='lg'>
                <NavbarBrand style={{marginRight: '5rem'}}>
                  <a href='/'>
                    <img src={branding} alt='WHOLEHEARTED' style={{height: `calc(${HEADER_HEIGHT}px - 2rem)`}} />
                  </a>
                </NavbarBrand>
                <NavbarToggler onClick={toggle} className='navbar-dark mr-0' />
                <Collapse isOpen={isOpen} navbar>
                  <Nav className='mr-auto' navbar>
                    <NavItem><NavLink href='#/vision'>VISION</NavLink></NavItem>
                    <NavItem><NavLink href='#/product'>PRODUCT</NavLink></NavItem>
                    <NavItem><NavLink href='#/experience'>EXPERIENCE</NavLink></NavItem>
                    <NavItem><NavLink href='#/contact-us'>CONTACT US</NavLink></NavItem>
                  </Nav>
                </Collapse>
              </Navbar>
            </Col>
          </Row>
        </Container>
      </header>
      <main>
        <HashRouter>
          <Switch>
            <Route path='/vision' exact component={Vision} />
            <Route path='/product' exact component={Products} />
            <Route path='/product/it-talents' exact component={ItTalents} />
            <Route path='/product/farming-equipment' exact component={FarmingEquipment} />
            <Route path='/product/covid19' exact component={Covid19Products} />
            <Route path='/experience' exact component={Experience} />
            <Route path='/contact-us' exact component={ContactUs} />
            <Route path='/'>
              <Redirect to='/vision' />
            </Route>
          </Switch>
        </HashRouter>
      </main>
      <footer style={FOOTER_STYLES}>
        <Container>
          <Row>
            <Col>
              <br/>
              Registration Number: 226-86-01361<br/>
              Company: whole hearted<br/>
              Address: Seoul, Kangseo-Gu, Yangcheon-Ro 482, 102<br/>
              Tel: +82-10-4219-5025
            </Col>
          </Row>
        </Container>
      </footer>
    </div>
  );
}

export default Layout;
