import React from 'react';
import farmingEquipment from "../assets/farming_equipment.jpg";
import {Col, Container, Row} from "reactstrap";
import fe1 from '../assets/fe1.jpg';
import fe2 from '../assets/fe2.png';
import testResult from '../assets/test_result.png';

function FarmingEquipment() {
  return (
    <div style={{backgroundImage: `url(${farmingEquipment})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
      <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
        <Container>
          <Row style={{padding: '3rem 0'}}>
            <Col style={{backgroundColor: 'rgba(255, 255, 255, 0.8)', padding: '1.5rem'}}>
              <section>
                <h2>Overseas Distributor of Only One Korea Product</h2>
                <br/>
                <p style={{fontWeight: 200}}>
                  MADE IN KOREA Quality<br/>
                  - Could not be compare with Chinese nor Vietnamese films<br/>
                  <br/>
                  - Exceptional Strength and Durability compare to Normal Greenhouse Covers (10 Times)<br/>
                  <br/>
                  Woven Cover for Agriculture<br/>
                  1. With Korean Pure HDPE Yarns<br/>
                  2. With Korean Pure Virgin LDPE Lamination<br/>
                  <br/>
                  <Row>
                    <Col>
                      <img src={fe1} style={{width: '23.6%'}} alt='1' />
                      <img src={fe2} style={{width: '75%'}} alt='2 3 4' /><br/>
                    </Col>
                  </Row>

                  <br/>
                  A. High Ligth Transmission and Perfect Light Diffusion<br/>
                  &emsp;Light Transmission 85%<br/>
                  &emsp;Light Diffusion 42%<br/>
                  <br/>
                  B. Better thermicity in the wintertime compare to normal poly<br/>
                  C. Gives great quality yield to the farms becaus of great ligh diffusion<br/>
                  D. Anti-Drip (Using Hi-Tech Nano Material)<br/>
                  E. Great in UV Cut in PAR<br/>
                  <br/>
                  F. APPLICATIONS<br/>
                  &emsp;Greenhouse Roof, Nursery, Greenhouse Sidewall, Corp Protection, AirDome<br/>
                  <br/>
                  <br/>
                  <br/>
                  <h2>Test and Comparisons</h2>
                  <img src={testResult} alt='TEST AND COMPARISONS' style={{width: '85%'}} />
                </p>
              </section>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default FarmingEquipment;
